import React from 'react';
import {
  Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button
} from 'reactstrap';

const Ads = (props) => {
  return (
    <div className="cards">
      <h6>Ads</h6>
      <Card>
        <CardImg top src="https://picsum.photos/200/300" alt="Card image cap" />
      </Card>
    </div>
  );
};

export default Ads;